@font-face {
    font-family: "roboto";
    src: url(../assets/fonts/Roboto-Italic.ttf);
}

$roboto: "roboto";

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background: rgb(33, 33, 33);
    color: rgb(243, 243, 243);
    font-family: $roboto;
}
li {
    list-style-type: none;
}